import { Injectable } from '@angular/core';

@Injectable()
export class DiagnosticoProvider {

  private _sintomas: any;

  public diagnostico(sintomas): Promise<any>{
    this._sintomas = this._getSintomas(sintomas);
    let mensagem = '';

    if ((this._temSintomaEspecifico('Febre') && this._temSintomaEspecifico('Dor de cabea') && this._temSintomaEspecifico('Dor no corpo'))) {
      mensagem = this._sindromeHemorragicalcterica();

    } else if ((this._temSintomaEspecifico('Febre')) && (this._temSintomaEspecifico('Tosse') || this._temSintomaEspecifico('Dor de garganta'))) {
      mensagem = this._sindromeRespiratoria();

    } else if (this._temSintomaEspecifico('Febre') && this._temSintomaEspecifico('Manchas avermelhadas na pele')){
      mensagem = this._sindromeExantematica();

    } else if (this._temSintomaEspecifico('Diarreia com sangue ou muco') || this._temSintomaEspecifico('Diarreia sem sangue nem muco')){
      mensagem = this._sindromeDiarreica();
    }

    return new Promise((resolve, reject) => {
      resolve(mensagem);
    });
  }

  private _getSintomas(sintomas){
    let tempSint = [];
    sintomas.forEach(element => {
      tempSint.push(
        element.nome);
    });
    return tempSint;
  }

  private _temSintomaEspecifico(sintoma){
    return this._sintomas.some(value => {
      return value == sintoma;
    });
  }

  private _sindromeHemorragicalcterica(){
    let mensagem = 'Aumente a ingesto de lquidos orais  e procure uma unidade de sade.';

    if (this._temSintomaEspecifico('Olhos avermelhados') || this._temSintomaEspecifico('Sangramento nasal') || this._temSintomaEspecifico('Pele amarelada') || this._temSintomaEspecifico('Sangramentos')){
      mensagem = 'Aumente a ingesto de lquidos orais, caso tenha tido contato com alagamentos, esgotos ou ratazanas, procure uma unidade de sade para avaliao mdica e informe.';

      if (this._temSintomaEspecifico('Falta de ar') || this._temSintomaEspecifico('Presso baixa') || this._temSintomaEspecifico('Vmitos')) {
        mensagem = 'Procure urgente uma unidade de sade para  atendimento mdico e avaliao de seu estado de sade. Caso tenha tido contato com alagamentos, esgotos ou ratazanas, informe o mdico.';
      }
    }

    return mensagem;

  }

  private _sindromeRespiratoria(){
    let regra = [
      {p:1, des:'Febre'}, {p:1, des: 'Tosse'}, {p:1, des:'Dor de garganta'},
      {p:3, des: 'Dor de cabea'}, {p:3, des:'Dor no corpo'}, {p:3, des:'Secreo no nariz'}, {p:3, des: 'Mal estar'},
      {p:12, des: 'Falta de ar'}, {p:12, des:'Cansao'}
    ];

    let mensagem = '';

    let testeSintomas = this._testeSintomas(regra);

    if (testeSintomas >=1 && testeSintomas <= 3){
      mensagem = 'Aumente a ingesto de lquidos orais  e procure uma unidade de sade.';
    } else if (testeSintomas >= 4 && testeSintomas <= 15) {
      mensagem = 'Aumente a ingesto de lquidos orais e procure uma unidade de sade para avaliao mdica.';
    } else if (testeSintomas > 12 && testeSintomas <= 39) {
      mensagem = 'Procure urgente uma unidade de sade para atendimento mdico e avaliao de seu estado de sade.';
    }

    return mensagem;
    // Mensagens 1 dia aps preenchimento dos sinais e sintomas
    // 3.4.1 | Mensagem para 3.3.1 = voc procurou uma unidade de sade? No se esquea de continuar ingerindo liquido |
    // 3.4.2 | Mensagem para 3.3.2 = voc j procurou uma unidade de sade? No se esquea de continuar ingerindo liquido |
    // 3.4.3 | Mensagem para 3.3.3 = voc j procurou uma unidade de sade?  muito importante que voc procure por atendimento mdico. |
  }

  private _sindromeExantematica(){
    let regra = [
      {p:1, des:'Febre'}, {p:1, des:'Manchas avermelhadas na pele'},
      {p:2, des: 'Coceira no corpo'}, {p:2, des: 'Irritao nos olhos'}, {p:2, des: 'Tosse'}, {p:2, des: 'Secreo no nariz'},
      {p:2, des: 'Ndulos no pescoo ou atrs das orelhas'}
    ];

    let mensagem = '';

    let testeSintomas = this._testeSintomas(regra);

    if (testeSintomas >= 4){
      mensagem = 'Evite permanecer em ambientes fechados e com aglomerado de pessoas. Procure o servio de sade mais prximo de sua residncia.';
    }

    return mensagem;

    // Mensagens 1 dia aps preenchimento dos sinais e sintomas |
    // 2.4.1 | Mensagem para 2.3.1 = Evite permanecer em ambientes fechados e com aglomerado de pessoas. Procure o servio de sade mais prximo de sua residncia.

  }

  private _sindromeDiarreica(){
    let regra = [{p:1, des:'Diarreia sem sangue nem muco'}, {p:10, des:'Diarreia com sangue ou muco'},
                {p:1, des:'Febre'}, {p:1, des:'Vmitos'}, {p:1, des:'Nuseas (enjoo)'}, {p:1, des:'Dor na barriga (clica intestinal)'}];
    let mensagem = '';
    let testeSintomas = this._testeSintomas(regra);

    if (testeSintomas > 1 && testeSintomas < 10){
      mensagem = 'Beba mais lquidos, como gua, gua de coco e chs aps evacuar, para evitar a desidratao.  No pare de comer e mantenha a sua alimentao habitual. Se houver piora da diarreia, sangue nas fezes, vmitos repetidos, muita sede, recusa de alimentos, diminuio do volume da urina ou piora no estado geral, procure o servio de sade para avaliao e tratamento adequado.';
    } else if (testeSintomas > 10){
      mensagem = 'Procure o servio de sade o quanto antes, para avaliao e tratamento adequado. At o atendimento, beba mais lquidos, como gua, gua de coco e chs,  para evitar a desidratao, no pare de comer e mantenha a sua alimentao habitual.';
    }

    return mensagem;

    // 4.4 | Mensagens 1 dia aps preenchimento dos sinais e sintomas |
    // 4.4.1 | Mensagem 4.3.3 = Continue monitorando os sinais e sintomas. Se houver piora da diarreia, sangue nas fezes, vmitos repetidos, muita sede, recusa de
    //                        alimentos, diminuio do volume da urina, ou piora no estado geral procure o servio de sade para avaliao e tratamento adequado.
  }

  private _testeSintomas (regra) {
    let pontos = 0;
    regra.forEach(value => {
      this._sintomas.forEach(sintoma => {
        if (sintoma == value.des) pontos = pontos + value.p
      });
    });
    return pontos
  }

}





