import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';

@Component({
  selector: 'page-dica-detalhe',
  templateUrl: 'dica-detalhe.html'
})

export class DicaDetalhePage {
// ``,
//   <a href="#">dfsf</a> ,

  detalhe: any = [
    {
      titulo: 'Viagens para reas com recomendao de vacinao contra febre amarela no Brasil:',
      texto: 'A vacina febre amarela  indicada para residentes e/ou viajantes que se destinam s reas com Recomendao de Vacinao (ACRV), com pelo menos 10 dias de antecedncia da data da viagem, tempo necessrio para que a vacina confira proteo contra a infeco. Confira aqui a Lista dos municpios com recomendao para vacinao contra febre amarela no Brasil.',
    },
    {
      titulo: 'Estrangeiros que iro visitar o Brasil:',
      texto: 'No Brasil no h obrigatoriedade de comprovao vacinal para entrada no pas, no entanto, o Ministrio da Sade do Brasil recomenda que os turistas internacionais atualizem a sua situao vacinal previamente  chegada ao Brasil, conforme as orientaes do calendrio de vacinao do pas de origem ou residncia.',
    }
  ];

  dica: any;

  constructor(public navCtrl: NavController,
              public navParams: NavParams) {
    this.dica = this.navParams.get('dica');
  }

  getClassColor (dica) {
    return 'lb-color-' + dica.titulo;
  }

  openWeb(){
    if (this.dica.link){
      window.open(this.dica.link, '_blank', 'location=yes');
    }
  }
}



